<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺管理</el-breadcrumb-item>
			  <el-breadcrumb-item :to="{name:'allowanceConfirmIndexLink'}">门店津贴确认</el-breadcrumb-item>
			  <el-breadcrumb-item>津贴分配确认</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" class="demo-form-inline">
							<el-row>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">门店：</label></el-col>
							  		<el-col :span="16">
							  			<el-form-item>
								  			<el-input v-model="pageData.tableObj.siteNo" :disabled="true" placeholder="请输入门店编号" />
									    </el-form-item>
							  		</el-col>
								</el-col>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">月份：</label></el-col>
							  		<el-col :span="16">
							  			<el-date-picker v-model="pageData.tableObj.yearMonthStr" :disabled="true" value-format="yyyyMM" type="month" placeholder="请选择日期"></el-date-picker>
							  		</el-col>
								</el-col>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">状态：</label></el-col>
							  		<el-col :span="16">
							  			<el-form-item>
							  				<el-input v-model="pageData.tableObj.status" :disabled="true" />
								  			<!--<el-select v-model="pageData.tableObj.status" :disabled="true">
										      <el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
										    </el-select>-->
									    </el-form-item>
							  		</el-col>
							  	</el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>

		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column sortable prop="yearmon" label="月份" min-width="90"></el-table-column>
					    <el-table-column sortable prop="employeeNo" label="工号" min-width="110"></el-table-column>
					    <el-table-column sortable prop="employeeName" label="员工姓名" min-width="120"></el-table-column>
					    <!--<el-table-column sortable prop="siteNo" label="店号" min-width="120"></el-table-column>-->
					    <!--<el-table-column sortable prop="siteName" label="店铺名称" min-width="120"></el-table-column>-->
					    <!--<el-table-column sortable prop="employeeSubGroup" label="员工子组" min-width="120"></el-table-column>-->
					    <!--<el-table-column sortable prop="entryTime" label="入职日期" min-width="120"></el-table-column>-->
					    <el-table-column sortable prop="organzationName" label="组织名称" min-width="120"></el-table-column>
					    <el-table-column sortable prop="postName" label="岗位名称" min-width="120"></el-table-column>
					    <el-table-column sortable prop="finalScore" label="月评分数" min-width="120"></el-table-column>
					    <el-table-column sortable prop="allowance" label="津贴总额" min-width="120"></el-table-column>
					    <el-table-column sortable prop="allowanceRate" label="津贴分配比例" min-width="120"></el-table-column>
					    <el-table-column sortable prop="issuedAllowance" label="应发津贴" min-width="120"></el-table-column>
					    <el-table-column sortable label="调整后津贴" min-width="120" fixed="right">
					    	<template slot-scope="scope">
					    		<el-input v-model="scope.row.adjustmentAllowance" :maxlength="8" placeholder="请输入调整后津贴" clearable />
						    </template>
					    </el-table-column>
					    <el-table-column sortable label="调整原因" min-width="180" fixed="right">
					    	<template slot-scope="scope">
					    		<el-input v-model="scope.row.adjustmentReason" :maxlength="200" placeholder="请输入调整原因" clearable />
						    </template>
					    </el-table-column>
					  </el-table>
					</div>
				</div>
			</div>
		</div>

		<el-row class="mb10">
			<el-col :span="24" class="text-center">
				<el-button type="success" icon="el-icon-edit-outline" @click.stop="updateEvt">提交</el-button>
				<router-link :to="{name:'allowanceConfirmIndexLink'}">
					<el-button type="danger" icon="el-icon-circle-close-outline">取消</el-button>
				</router-link>
			</el-col>
		</el-row>


	</el-main>
</template>
<script type="text/javascript">
	export default{
		data(){
			return {
				pageData:{
					isDetail:false,
					statusList:[
						{name:"全部",value:""},
						{name:"已导入",value:2},
						{name:"可修改",value:3},
						{name:"已关闭",value:4},
					],
					tableObj:{
						mainId:"",
		      			siteNo:"",
			      		yearMonthStr:"",
			      		status:"", // 状态
		      			currentPage: 1,
		      			pageSize: 10,
		      			totalPage: 1, // 多少页
		      			totalRows: 1, // 总数
		      			pageArray:[10,20,30,40,50],
		      			list: []
		      		},
				}
			}
		},
		mounted(){
			this.initFunc()
		},
		methods:{
			initFunc(){
				this.pageData.tableObj.mainId = this.$route.query.mainId
				this.pageData.tableObj.siteNo = this.$route.query.siteNo
	      		this.pageData.tableObj.yearmon = this.$route.query.yearmon
//	      		this.pageData.tableObj.status = this.$route.query.status
	      		let item = this.pageData.statusList.find(item=>{
	      			return item.value == this.$route.query.status
	      		})
	      		this.pageData.tableObj.status = item.name
				this.queryEvt()
			},
			queryEvt(){
				let obj = {
					mainId:this.pageData.tableObj.mainId,
				}
				const loading = this.$loading({
		          lock: true,
		          spinner: 'wm-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        })
		        fetch(this.$api.urls.POST_ALLOWANCE_QUERYITEM,{
		        	method:'post',
		        	headers:{
		      			"Content-type":"application/json",
		      		},
		      		credentials: "include",
		        	body:JSON.stringify(obj)
		        })
		        .then(res=>res.json())
		        .then(res=>{
		        	setTimeout(() => {
			          loading.close();
			        }, 500)
			        if(res.success){
			        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
		      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
		      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
		      			this.pageData.tableObj.totalRows = res.model.count // 总数
		      			this.pageData.tableObj.list = res.model.map(item=>{
		      				item.entryTime = this.$api.dateFormat("yyyy-MM-dd",item.entryTime)
		      				return item
		      			})
			        }else{
			        	this.$message.error(res.message)
			        }
		        }).catch(err=>{
		        	setTimeout(() => {
			          loading.close();
			        }, 500)
		        	this.$message.error("系统错误："+err)
		        })
			},
			updateEvt(){
				let obj = this.pageData.tableObj.list.map((item)=>{
					return {
						id :item.id,
						issuedAllowance :item.issuedAllowance,
						adjustmentAllowance :item.adjustmentAllowance,
						adjustmentReason :item.adjustmentReason,
					}
				})
				let isAdd = {
					ok:false,
					msg:''
				}
				obj.some(item=>{
					if(item.adjustmentAllowance !=="" || item.adjustmentReason !== ""){
						if(item.adjustmentAllowance === "") {
							isAdd = {
								ok:true,
								msg:'调整后津贴不能为空'
							}
							return true
				        }
						if(!/^(([1-9][0-9]*\.[0-9][0-9]*)|([0]\.[0-9][0-9]*)|([1-9][0-9]*)|([0]{1}))$/.test(item.adjustmentAllowance)) {
							isAdd = {
								ok:true,
								msg:'调整后津贴必须是正数'
							}
							return true
				        }
						if(item.adjustmentReason === "") {
							isAdd = {
								ok:true,
								msg:'调整原因不能为空'
							}
							return true
				        }
					}
				})
				if(isAdd.ok){
					this.$message({
						dangerouslyUseHTMLString: true,
						message: `<h5>${isAdd.msg}</h5><p>如您填写了（调整后津贴、调整原因）,必须一行填写完成!</p>`,
						type: 'warning'
					})
					return false
				}
				const loading = this.$loading({
		          lock: true,
		          spinner: 'wm-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        })
		        fetch(this.$api.urls.POST_ALLOWANCE_ADJUSTMENT,{
		        	method:'post',
		        	headers:{
		      			"Content-type":"application/json",
		      		},
		      		credentials: "include",
		        	body:JSON.stringify(obj)
		        })
		        .then(res=>res.json())
		        .then(res=>{
		        	setTimeout(() => {
			          loading.close();
			        }, 500)
			        if(res.success){
			        	this.$alert("提交成功", '提示', {
			        		center: true,
				        	dangerouslyUseHTMLString: true,
				        	callback: action => {
					           this.$router.push({name:'allowanceConfirmIndexLink'})
					        }
				        })
			        }else{
			        	this.$message.error(res.message)
			        }
		        }).catch(err=>{
		        	setTimeout(() => {
			          loading.close();
			        }, 500)
		        	this.$message.error("系统错误："+err)
		        })
			}
			
		}
	}
</script>



