<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺管理</el-breadcrumb-item>
			  <el-breadcrumb-item>门店津贴确认</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" class="demo-form-inline">
							<el-row>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">门店：</label></el-col>
							  		<el-col :span="16">
							  			<el-form-item>
								  			<el-input :disabled="true" v-model="pageData.tableObj.siteNo" placeholder="请输入门店编号" clearable />
									    </el-form-item>
							  		</el-col>
								</el-col>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">状态：</label></el-col>
							  		<el-col :span="16">
							  			<el-form-item>
								  			<el-select v-model="pageData.tableObj.status">
										      <el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
										    </el-select>
									    </el-form-item>
							  		</el-col>
							  	</el-col>
								<el-col :span="12">
							  		<el-col :span="4"><label class="control-label">提交时间：</label></el-col>
							  		<el-col :span="20">
							  			<el-col :span="10">
							  				<el-date-picker v-model="pageData.tableObj.startTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
							  			</el-col>
							  			<el-col :span="2" class="text-center" style="line-height: 40px;">-</el-col>
							  			<el-col :span="10">
							  				<el-date-picker v-model="pageData.tableObj.endTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
							  			</el-col>
							  		</el-col>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">月份：</label></el-col>
							  		<el-col :span="16">
							  			<el-date-picker v-model="pageData.tableObj.yearmon" value-format="yyyyMM" type="month" placeholder="请选择日期"></el-date-picker>	
							  		</el-col>
								</el-col>
								<el-col :span="6" class="text-center">
							  		<el-form-item>
									    <el-button type="primary" @click.stop="queryEvt" icon="el-icon-search">查询</el-button>
									</el-form-item>
							  	</el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column sortable prop="siteNo" label="门店号" min-width="90"></el-table-column>
					    <el-table-column sortable prop="siteName" label="门店名称" min-width="110"></el-table-column>
					    <el-table-column sortable prop="yearmon" label="月份" min-width="110"></el-table-column>
					    <el-table-column sortable prop="personNmu" label="导入人数" min-width="120"></el-table-column>
					    <el-table-column sortable prop="statusName" label="状态" min-width="120"></el-table-column>
					    <el-table-column sortable prop="created" label="提交时间" min-width="130"></el-table-column>
					    <el-table-column sortable prop="creator" label="导入人" min-width="110"></el-table-column>
					    <el-table-column fixed="right" label="操作" min-width="100">
					      <template slot-scope="scope">
					        <el-button @click.stop="detailEvt(scope.$index, scope)" type="text" size="small">查看详情</el-button>
					      </template>
					    </el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					    	:current-page="pageData.tableObj.currentPage" 
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>

	</el-main>
</template>
<script type="text/javascript">
	export default{
		data(){
			return {
				pageData:{
					statusList:[
						{name:"全部",value:""},
						{name:"已导入",value:2},
						{name:"可修改",value:3},
						{name:"已关闭",value:4},
					],
					tableObj:{
						siteNo :"", // 门店号码
						siteName:'',
      					yearmon :"", // 月份
						status :"", //  状态
						startTime :"", //  开始时间
						endTime :"", //  结束时间
						pageSize :10, //  单页行数
						currentPage :1, //  当前页吗
		      			totalPage: 1, // 多少页
		      			totalRows: 1, // 总数
		      			pageArray:[10,20,30,40,50],
		      			list: []
		      		},
				}
			}
		},
		created(){
    	this.initFunc()
    },
    methods: {
    	initFunc(){
    		this.commonInfo()
    	},
    	commonInfo(){
			const loading = this.$loading({
		        lock: true,
		        spinner: 'wm-icon-loading',
		        background: 'rgba(0, 0, 0, 0.7)'
		      })
					var obj ={}
		  		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
		      	method:'post',
		      	headers:{
		    			"Content-type":"application/json",
		    		},
		    		credentials: "include",
		      	body:JSON.stringify(obj)
		      })
		      .then(res=>res.json())
		      .then(res=>{
		      	setTimeout(() => {
		          loading.close();
		        }, 500)
		      	if(res.success){
		      		this.pageData.tableObj.mandt = res.model.mandt
		      		this.pageData.tableObj.siteNo = res.model.siteNo
		      		this.pageData.tableObj.siteName = res.model.siteName
		      		this.queryEvt()	
		      	}else{
		      		this.$message.error(res.message)
		      	}
		      }).catch(err=>{
		      	setTimeout(() => {
		          loading.close();
		        }, 500)
		      	this.$message.error("错误："+err)
		      })
		
		},
		queryEvt() {
      	let obj = {
      		siteNo : this.pageData.tableObj.siteNo,
      		yearMonthStr :this.pageData.tableObj.yearMonthStr,
			status :this.pageData.tableObj.status,
			startTime :this.pageData.tableObj.startTime, 
			endTime :this.pageData.tableObj.endTime, 
			currentPage :this.pageData.tableObj.currentPage, 
			pageSize :this.pageData.tableObj.pageSize 
      	}
      	if(this.$api.compareDate(obj.startTime,obj.endTime)){
      		this.$message({
	          message: '开始时间不能大于结束时间!',
	          type: 'warning'
	        })
	        return false
      	}
      	const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        fetch(this.$api.urls.POST_ALLOWANCE_QUERYMAIN,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
      			this.pageData.tableObj.totalRows = res.model.count // 总数
	        	
	        	this.pageData.tableObj.list = res.model.data.map((item)=>{
	        		this.pageData.statusList.find(el=>{
	        			if(item.status == el.value){
	        				return item.statusName = el.name
	        			}
	        		})
	        		item.siteNo = this.pageData.tableObj.siteNo
		      		item.siteName = this.pageData.tableObj.siteName
	        		item.created = this.$api.dateFormat("yyyy-MM-dd",item.created)
	        		return item
	        	})
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
        
      },
      detailEvt(index,item){
      	this.$router.push({name:'allowanceConfirmUploadLink',query:{
      		siteNo:item.row.siteNo,
      		yearmon:item.row.yearmon,
      		status:item.row.status,
      		mainId:item.row.id
      	}})
      },
			handleSizeChange(val) {
	        this.pageData.tableObj.pageSize = val // 单页行数
	        this.queryEvt()
	    },
	    handleCurrentChange(val) {
	        this.pageData.tableObj.currentPage = val // 当前页
	        this.queryEvt()
	    }
		}
	}
</script>


