<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺配置</el-breadcrumb-item>
			  <el-breadcrumb-item>小店维护</el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.tableObj" class="demo-form-inline">
							<el-row>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">门店号码：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-input v-model="pageData.tableObj.siteNo" placeholder="请输入门店号" clearable />
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  			<el-col :span="8"><label class="control-label">小店类型：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.tableObj.shopType">
									      <el-option label="全部" value=""></el-option>
									      <el-option v-for="item in pageData.shopTypeList" :key="item.shopType" :label="item.induName+item.shopType" :value="item.shopType"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="12">
							  		<el-col :span="4"><label class="control-label">创建时间：</label></el-col>
							  		<el-col :span="20">
							  			<el-col :span="10">
							  				<el-date-picker v-model="pageData.tableObj.startTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
							  			</el-col>
							  			<el-col :span="2" class="text-center" style="line-height: 40px;">-</el-col>
							  			<el-col :span="10">
							  				<el-date-picker v-model="pageData.tableObj.endTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
							  			</el-col>
							  		</el-col>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="24" class="text-center">
						  		<el-form-item>
								    <el-button type="primary" @click.stop="queryEvt" icon="el-icon-search">查询</el-button>
								  </el-form-item>
							  </el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-setting" @click.stop="updateEvt">调整架构</el-button>
						</el-col>
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column sortable prop="id" label="序号" min-width="90"></el-table-column>
					    <el-table-column sortable prop="siteNo" label="门店编号" min-width="90"></el-table-column>
					    <el-table-column sortable prop="siteName" label="门店名称" min-width="90"></el-table-column>
					    <el-table-column sortable prop="shopNo" label="小店编号" min-width="130"></el-table-column>
					    <el-table-column sortable prop="shopName" label="小店名称" min-width="130"></el-table-column>
					    <el-table-column sortable prop="chargeName" label="小店负责人" min-width="130"></el-table-column>
					    <el-table-column sortable prop="shopType" label="架构类型" min-width="120"></el-table-column>
					    <el-table-column sortable prop="creator" label="创建人" min-width="110"></el-table-column>
					    <el-table-column sortable prop="created" label="创建时间" min-width="120"></el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					    	:current-page="pageData.tableObj.currentPage" 
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>
	</el-main>
</template>

<script>
export default {
    data() {
      return {
      	pageData:{
	      	shopTypeList:[], // 小店类型
	      	tableObj:{
	      		status:-1,
	      		siteNo :"", //  门店编号
				shopType :"", //  小店-类型 
				mandt:"", // 大区号
				startTime:'',
				endTime:'',
      			currentPage: 1,
      			pageSize: 10,
      			totalPage: 1, // 多少页
      			totalRows: 1, // 总数
      			pageArray:[10,20,30,40,50],
      			list: []
      		}
      	}
      }
    },
    created(){
			this.commonInfo()
    },
    computed:{
    },
    methods: {
    	updateEvt(){
    		this.$router.push({
    			name:'smallShopUpdateLink'
    		})
    	},
    	initFunc(){
    		this.shopTypeListEvt()
    	},
    	commonInfo(){
				const loading = this.$loading({
	        lock: true,
	        spinner: 'wm-icon-loading',
	        background: 'rgba(0, 0, 0, 0.7)'
	      })
				let obj ={}
	  		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
	      	method:'post',
	      	headers:{
	    			"Content-type":"application/json",
	    		},
    			credentials: "include",
	      	body:JSON.stringify(obj)
	      })
	      .then(res=>res.json())
	      .then((res)=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	if(res.success){
	      		this.$store.dispatch("setIndu",res.model.indu)
	      		this.pageData.tableObj.mandt = res.model.mandt
	      		this.pageData.tableObj.siteNo = res.model.siteNo
	      		this.initFunc()
	      	}else{
	      		this.$message.error(res.message)
	      	}
	      }).catch(err=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	this.$message.error("错误："+err)
	      })
		
			},
    	
    	shopTypeListEvt(){
    		var obj  = {
    			mandt:this.pageData.tableObj.mandt,
    			siteNo:this.pageData.tableObj.siteNo
    		}
    		fetch(this.$api.urls.POST_PARTNER_QUERYSHOPTYPE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	if(res.success){
        		this.pageData.shopTypeList = res.model
        		this.queryEvt()
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	this.$message.error("系统错误："+err)
        })
    	},
      queryEvt() {
      	
      	let obj = {
      		status:this.pageData.tableObj.status,
      		siteNo :this.pageData.tableObj.siteNo, //  门店编号 无
			shopType :this.pageData.tableObj.shopType, //  小店-类型 无
			mandt :this.pageData.tableObj.mandt, // 大区号
			startTime:this.pageData.tableObj.startTime,
			endTime:this.pageData.tableObj.endTime,
			currentPage :this.pageData.tableObj.currentPage, 
			pageSize :this.pageData.tableObj.pageSize 
      	}
      	if(this.$api.compareDate(obj.startTime,obj.endTime)){
      		this.$message({
	          message: '开始时间不能大于结束时间!',
	          type: 'warning'
	        })
	        return false
      	}
      	const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        fetch(this.$api.urls.POST_PARTNER_QUERYSITESHOP,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
      			this.pageData.tableObj.totalRows = res.model.count // 总数
      			const data =res.model.data
	        	for(let i =0;i<data.length;i++){
	        		data[i].created = this.$api.dateFormat("yyyy-MM-dd",data[i].created)
	        	}
	        	this.pageData.tableObj.list = data
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
        
      },
      handleSizeChange(val) {
        this.pageData.tableObj.pageSize = val // 单页行数
        this.queryEvt()
      },
      handleCurrentChange(val) {
        this.pageData.tableObj.currentPage = val // 当前页
        this.queryEvt()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



