<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  	<el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  	<el-breadcrumb-item>店铺配置</el-breadcrumb-item>
			  	<el-breadcrumb-item :to="{name:'smallShopIndexLink'}">小店维护</el-breadcrumb-item>
			  	<el-breadcrumb-item>类型调整</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" class="demo-form-inline">
							<el-row>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">门店编号：</label></el-col>
							  		<el-col :span="16">
							  			<el-form-item>
								  			<el-input v-model="pageData.tableObj.siteNo" @change="queryEvt" placeholder="请输入门店编号" />
									    </el-form-item>
							  		</el-col>
							  	</el-col>
								<el-col :span="6">
							  		<el-col :span="8"><label class="control-label">类型：</label></el-col>
							  		<el-col :span="16">
							  			<el-select v-model="pageData.tableObj.shopType" @change="queryEvt" placeholder="请选择">
							  				<el-option key="" label="全部" value=""></el-option>
    										<el-option v-for="item in pageData.shopTypeList" :key="item.shopType" :label="item.induName+item.shopType" :value="item.shopType"></el-option>
  										</el-select>
							  		</el-col>
								</el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>

		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%" :row-class-name="tableRowClassName">
						    <el-table-column sortable prop="shopNo" label="小店编号" min-width="120"></el-table-column>
						    <el-table-column sortable prop="shopName" label="小店名称" min-width="120"></el-table-column>
						    <el-table-column label="负责人编号" min-width="120">
						    	<template slot-scope="scope">
						    		<el-input v-model="scope.row.chargeNo" />
							    </template>
						    </el-table-column>
						    <el-table-column label="负责人" min-width="120">
						    	<template slot-scope="scope">
						    		<el-input v-model="scope.row.chargeName" />
							    </template>
						    </el-table-column>
						</el-table>
					</div>
				</div>
			</div>
		</div>
		
		<el-row class="mb10">
			<el-col :span="24" class="text-center">
				<el-button type="success" icon="el-icon-edit-outline" @click.stop="updateEvt">提交</el-button>
				<router-link :to="{name:'smallShopIndexLink'}">
					<el-button type="danger" icon="el-icon-circle-close-outline">取消</el-button>
				</router-link>
			</el-col>
		</el-row>
		
	</el-main>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
				pageData:{
					shopTypeList:[], // 小店类型
					tableObj:{
						mandt:"",
						indu:"",
						siteNo:"",
						shopType:"",
						list: []
					},
				}
			}
		},
		created(){
			this.commonInfo()
    	},
    	mounted(){
		},
		methods: {
			initFunc(){
				this.shopTypeListEvt()
		
			},
			commonInfo(){
				const loading = this.$loading({
			        lock: true,
			        spinner: 'wm-icon-loading',
			        background: 'rgba(0, 0, 0, 0.7)'
			      })
						let obj ={}
			  		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
			      	method:'post',
			      	headers:{
			    			"Content-type":"application/json",
			    		},
		    			credentials: "include",
			      	body:JSON.stringify(obj)
			      })
			      .then(res=>res.json())
			      .then((res)=>{
			      	setTimeout(() => {
			          loading.close();
			        }, 500)
			      	if(res.success){
			      		this.pageData.tableObj.mandt = res.model.mandt
			      		this.pageData.tableObj.indu = res.model.indu
			      		this.pageData.tableObj.siteNo = res.model.siteNo
			      		this.initFunc()
			      	}else{
			      		this.$message.error(res.message)
			      	}
			      }).catch(err=>{
			      	setTimeout(() => {
			          loading.close();
			        }, 500)
			      	this.$message.error("错误："+err)
			      })
		
			},
			shopTypeListEvt(){
	    		var obj  = {
	    			mandt:this.pageData.tableObj.mandt,
	    			siteNo:this.pageData.tableObj.siteNo
	    		}
	    		fetch(this.$api.urls.POST_PARTNER_QUERYSHOPTYPE,{
		        	method:'post',
		        	headers:{
		      			"Content-type":"application/json",
		      		},
		      		credentials: "include",
		        	body:JSON.stringify(obj)
		        })
		        .then(res=>res.json())
		        .then(res=>{
		        	if(res.success){
		        		this.pageData.shopTypeList = res.model
		        	}else{
		        		this.$message.error(res.message)
		        	}
		        }).catch(err=>{
		        	this.$message.error("系统错误："+err)
		        })
	    	},
			queryEvt() {
				let obj = {
					siteNo:this.pageData.tableObj.siteNo,
					mandt:this.pageData.tableObj.mandt,
					indu:this.pageData.tableObj.indu,
					shopType:this.pageData.tableObj.shopType
				}
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
				})
				fetch(this.$api.urls.POST_SMALLSHOP_QUERYSITESHOP,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
					loading.close();
					}, 500)
					if(res.success){
						this.pageData.tableObj.list = res.model.map((item)=>{
							item.yearMonthStr = this.pageData.tableObj.yearMonthStr
							return item
						})
					}else{
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
					loading.close();
					}, 500)
					this.$message.error("系统错误："+err)
				})

			},
			tableRowClassName(item){
				if(item.row.chargeName){
					return ''
				}else{
					return 'warning-row'
				}
			},
			updateEvt(){
				let obj = this.pageData.tableObj.list.map(item=>{
					item.siteNo = this.pageData.tableObj.siteNo
					return item 
				})
				if(obj.length<=0){
					this.$message({
						message: '请查询填写后再提交保存!',
						type: 'warning'
					})
					return false
				}
				let isAdd = obj.some((item)=>{
					return item.chargeName == "" || item.chargeName == null || item.chargeNo == "" || item.chargeNo == null
				})
				if(isAdd){
					this.$message({
						message: '请填写负责人姓名!',
						type: 'warning'
					})
					return false
				}
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				})
				fetch(this.$api.urls.POST_SMALLSHOP_SAVECHANGEDSITESHOP,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
						this.$alert("提交成功", '提示', {
			        		center: true,
				        	dangerouslyUseHTMLString: true,
				        	callback: action => {
					           this.$router.push({name:'smallShopIndexLink'})
					        }
				        })
					}else{
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					this.$message.error("系统错误："+err)
				})
			}
			
		}
	}
</script>
<style>
	.el-table tr.warning-row{
		background-color: #e6a23c !important;
	}
</style>



